@import url('https://fonts.googleapis.com/css2?family=Titan+One&display=swap');

header {
    width: 100%;
    background-color: var(--blue-background);
    color: white;
    font-size: 40px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 15px 40px;
    font-family: 'Titan One', cursive;
    /*height: 140px; Se especifica altura para tener el valor a restar a e.pageY y colocar Magic Div*/
    position: sticky;
    top: 0px;
    z-index: 10;
    height: 76px;
}

.open-close-characters {
    width: 45px;
    height: 45px;
    position: relative;
    cursor: pointer;
}

.svg-header {
    width: 100%;
    height: 100%;
    color: white;
    position: absolute;
    pointer-events: none;
    left: 0px;
}

.open-svg {
    opacity: 1;
}

.close-svg {
    opacity: 0;
}

.svg-show {
    opacity: 1;
}

.svg-hide {
    opacity: 0;
}

.timer__home-container {
    display: flex;
    flex-direction: row;
    gap: 15px;
    align-items: center;
    justify-content: center;
}

.timer__home-container .home__button {
    font-size: 18px;
    font-weight: normal;
    padding: 5px 8px;
}

.timer__home-container .home__button svg {
    width: 25px;
    height: 25px;
}


@media (max-width: 700px) {
    header {
        padding: 15px 20px;
        font-size: 30px;
    }

    .timer__home-container .home__button span {
        display: none;
    }

    .timer__home-container .home__button {
        border-radius: 50%;
        width: 40px;
        height: 40px;
        padding: 0px;
    }
}